const onResponse = (res) => {
  return res.ok
    ? res.json()
    : Promise.reject('Error');
};

export const addNewUSer = (body) => {
  return fetch('http://localhost:3050/users', {
    headers: {
      "Content-Type": "application/json"
    },
    method: "POST",
    body: JSON.stringify(body),
    
  }).then((res) => onResponse(res));
}